<template>
  <v-app class="no-web3">
    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs12>
          <v-card>
            <v-card-text>
              <p>Esta aplicacin corre sobre Rinkeby, una blockchain de Ethereum usada para pruebas. Para poder usarla, segu las siguientes instrucciones:</p>

              <ol>
                <li>Instal <a href="https://metamask.io/" target="_blank">MetaMask</a> en tu navegador</li>
                <li>Seleccion "Rinkeby Test Network" en la lista de redes (arriba a la izquierda)</li>
                <li>Ingres un password y la confirmacin, y guard en algn lugar la lista de doce palabras que va a generar tu cuenta (como estamos usando una blockchain de prueba, no es muy importante que esto est en un lugar seguro)</li>
                <li>Despus del paso anterior vas a tener una direccin creada en la blockchain de Rinkeby.
                  Pods copiarla desde metamask clickeando en los tres puntos a la derecha de tu cuenta.
                  Una vez que la tengas, pods conseguir Ether para Rinkeby usando <a href="https://faucet.rinkeby.io/" target="_blank">este link</a>.
                  Si este paso te parece muy complicado, enviale tu direccin a alguno de los organizadores y pedile que te enve un poco.</li>
                <li>Recarg la pgina y anotate para el sorteo. Cuando clickees "Comprar ticket", MetaMask va a pedirte que confirmes la transaccin.</li>
                <li>Tu nombre va a aparecer en la lista en aproximadamente un minuto</li>
              </ol>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  name: 'NoWeb3'
}
</script>

<style></style>



